import SimplexNoise from 'simplex-noise';
import Entity from './setup'

let simplex = new SimplexNoise(Math.random);

// Taken from: https://al-ro.github.io/projects/embers/
const computeCurl = (x, y, z) => {
    var eps = 0.0001;

    var curl = new THREE.Vector3();

    //Find rate of change in YZ plane
    var n1 = simplex.noise3D(x, y + eps, z);
    var n2 = simplex.noise3D(x, y - eps, z);
    //Average to find approximate derivative
    var a = (n1 - n2) / (2 * eps);
    var n1 = simplex.noise3D(x, y, z + eps);
    var n2 = simplex.noise3D(x, y, z - eps);
    //Average to find approximate derivative
    var b = (n1 - n2) / (2 * eps);
    curl.x = a - b;

    //Find rate of change in XZ plane
    n1 = simplex.noise3D(x, y, z + eps);
    n2 = simplex.noise3D(x, y, z - eps);
    a = (n1 - n2) / (2 * eps);
    n1 = simplex.noise3D(x + eps, y, z);
    n2 = simplex.noise3D(x + eps, y, z);
    b = (n1 - n2) / (2 * eps);
    curl.y = a - b;

    //Find rate of change in XY plane
    n1 = simplex.noise3D(x + eps, y, z);
    n2 = simplex.noise3D(x - eps, y, z);
    a = (n1 - n2) / (2 * eps);
    n1 = simplex.noise3D(x, y + eps, z);
    n2 = simplex.noise3D(x, y - eps, z);
    b = (n1 - n2) / (2 * eps);
    curl.z = a - b;

    return curl;
}

console.log(computeCurl(0, 0, 0))

class Tube extends Entity {
    constructor() {
        super();
    }

    Start() {
        super.Start();
        for (let i = 0; i < 40; i++) {
            let path = new THREE.CatmullRomCurve3(this._getCurve(new THREE.Vector3(i / 100, 0, 0)));
            let g = new THREE.TubeBufferGeometry(path, 600, 0.005, 8, false);
            let curve = new THREE.Mesh(g, this.material);
            this._scene.scene.add(curve);
        }
    }

    Update(time) {
        // this.mesh.rotation.y += 0.01;
        // this.mesh.rotation.x += 0.01;
        super.Update();
    }

    _getCurve(initial = new THREE.Vector3(0,0,0)) {
        let points = [initial];
        let currPoint = initial.clone();
        for (let i = 0; i < 2000; i++) {
            let p = computeCurl(currPoint.x, currPoint.y, currPoint.z)
            currPoint.addScaledVector(p,0.001)
            points.push(currPoint.clone())
        }
        return points;
    }

    BuildMesh() {
        let path = new THREE.CatmullRomCurve3(this._getCurve())
        this.geometry = new THREE.TubeBufferGeometry(path, 600, 0.005, 8, false);
        this.material = new THREE.MeshNormalMaterial();
        this.mesh = new THREE.Mesh(this.geometry, this.material);
    }
}

new Tube();
window.addEventListener('mousedown', () => {
    document.getElementById('description').className = "dimmed"
})
window.addEventListener('mouseup', () => document.getElementById('description').className = "")



